<template>
	<button class="todo-item" v-bind:class="{'is-complete': item.completed}" v-on:click="markComplete">
		{{item.title}}
	</button>
</template>

<script>
	export default {
		name: "TodoItem",
		props: ["item"],
		methods: {
			markComplete() {
				this.item.completed = !this.item.completed;
			}
		}
	}
</script>

<style scoped>
	.todo-item {
		background: #f4f4f4;
		padding: 10px;
		border-bottom: 1px #ccc dotted;
		width: 100%;
	}

	.is-complete {
		text-decoration: line-through;
	}
</style>